import { PurchaseInvoiceItem } from "./purchaseinvoiceitem.entity";
import { SupplierAddress } from "./supplieraddress.entity";
import { SupplierManagement } from "./suppliermanagement";
import { Customer } from "./customer.entity";

export class PurchaseInvoice {
  id: string;
  companyId: number;
  supplierId: number;
  gstin: string;
  supplier: SupplierManagement;
  invoiceDate: Date;
  supplierInvoiceNumber: string;
  purchasingInformation: string;
  purchaseInvoiceNo: any;
  purchaseSubTotal: number;
  purchaseTaxAmount: number;
  totalPurchaseAmount: number;
  isTaxable: number;
  customer: Customer;
  purchaseInvoiceStockItemList: PurchaseInvoiceItem[] = [];
}



