import { SupplierManagement } from "./suppliermanagement";

export class StockItemPrice {
  id: null;
  allownegativestock: boolean;
  batchNumber: number;
  cpDateFrom: Date;
  cpDateTo: Date;
  initialQty: number;
  itemCostPrice: number;
  lowstockalert: number;
  minStock: number;
  mrp: number;
  mrpDateFrom: Date;
  mrpDateTo: Date;
  overStock: number;
  safetyStock: number;
  sellingPrice: number;
  spDateFrom: Date;
  spDateTo: Date;
  stockasofdate: Date;
  stockathand: number;
  tax: number;
  taxInclusive: boolean;
  //discountRule: number;
  discount: number;
  supplierId: number;
  supplier: SupplierManagement = new SupplierManagement();
}



