import { Address } from "./address.entity";
import { CustomerPayment } from "./customerpayment.entity";
import { CustomerTax } from "./customertax.entity";

export class Customer {
  id: string;
  firstName: string;
  lastName: string;
  phone: string;
  mobile: string;
  accountNumber: string;
  gstRegNumber: string;
  companyName: string;
  mobileNumber: string;
  supplierInvoiceNumber: string;
  supplierId: string;
  displayName: string;
  gstNo: string;
  purchaseInvoice: string;
  email: string;
  fax: string;
  address: Address = new Address();
  inlineAddress: string;
  webSite: string;
  businessName: string;
  shippingAddress: Address = new Address();
  billingAddress: Address = new Address();
  customerPayment: CustomerPayment;
  customerTax: CustomerTax;
  Gstin: string;
  ecommerceGSTIN: string;
  discount: number = 0;
}



