import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from "@angular/forms";
import { AppFormGroup } from "../shared/common/app.form";
export class InvoiceFormGroup extends AppFormGroup {
  static init() {
    return this.getFormGroup();
  }
  static edit(group: FormGroup) {
    group.addControl(
      "sales_firstName",
      this.getController(null, this.TYPE_DATA, null, 99)
    );
    group.addControl(
      "sales_mobile",
      this.getController(null, this.TYPE_NUMBER, 10, 10)
    );
    group.addControl(
      "sales_email",
      this.getController(null, this.TYPE_EMAIL, null, 50)
    );
    group.addControl(
      "sales_website",
      this.getController(null, this.TYPE_WEBSITE, null, 99)
    );
    group.addControl(
      "sales_termsOfPayment",
      this.getController(null, this.TYPE_DATA, null, 99)
    );
    group.addControl(
      "sales_invoiceDate",
      this.getController(null, this.TYPE_DATE, null, 30)
    );
    group.addControl(
      "sales_dueDate",
      this.getController(null, this.TYPE_DATE, null, 30)
    );
    group.addControl(
      "sales_invoiceMessage",
      this.getController(null, this.TYPE_DATA, null, 99)
    );
    group.addControl(
      "sales_memo",
      this.getController(null, this.TYPE_DATA, null, 99)
    );
    group.addControl(
      "sales_purchaseOrder",
      this.getController(null, this.TYPE_ALFA_NUM, null, 30)
    );
    group.addControl(
      "sales_invoiceType",
      this.getController(true, this.TYPE_ALL, null, 64)
    );
  }
  static ecommerce(group: FormGroup, add: boolean) {
    if (add) {
      group.addControl(
        "sales_ecommerceGSTIN",
        this.getController(false, this.GST_CODE_PATTERN, 15, 15)
      );
    } else {
      group.removeControl("sales_ecommerceGSTIN");
    }
  }
  static address(group: FormGroup, add: boolean) {
    if (add) {
      group.addControl(
        "sales_address_lane",
        this.getController(null, this.TYPE_ALFA_NUM_DES_SPACE, null, 20)
      );
      group.addControl(
        "sales_address_area",
        this.getController(null, this.TYPE_ALFA_NUM_DES_SPACE, null, 20)
      );
      group.addControl(
        "sales_address_city",
        this.getController(null, this.TYPE_ALFA_DES, null, 20)
      );
      group.addControl(
        "sales_address_state",
        this.getController(null, this.TYPE_DATA, null, 20)
      );
      group.addControl(
        "sales_address_street",
        this.getController(null, this.TYPE_ALFA_NUM_DES_SPACE, null, 20)
      );
      group.addControl(
        "sales_address_zipCode",
        this.getController(null, this.TYPE_NUMBER, 6, 8)
      );
    } else {
      group.removeControl("sales_address_lane");
      group.removeControl("sales_address_area");
      group.removeControl("sales_address_city");
      group.removeControl("sales_address_state");
      group.removeControl("sales_address_street");
      group.removeControl("sales_address_zipCode");
    }
  }
  static email(group: FormGroup) {
    group.addControl(
      "email_to",
      this.getController(null, this.TYPE_EMAIL, null, 300)
    );
    group.addControl(
      "email_subject",
      this.getController(null, this.TYPE_DATA, null, 300)
    );
    group.addControl(
      "email_message",
      this.getController(null, this.TYPE_ALL, null, 3000)
    );
  }
}



