import { Item } from "./item.entity";
import { Address } from "./address.entity";
import { Customer } from "./customer.entity";

export class Sales {
  id: string;
  customer: Customer = new Customer();
  //company: any;
  companyId: any;
  //user: any;
  userId: any;
  memo: string;
  invoiceNumber: any;
  invoiceMessage: string;
  status: any = "DRAFTSAVE";
  isBillingAddress: boolean = true;
  isECommerce: boolean = true;
  isCgst: boolean = false;
  isProduct: boolean = false;
  isService: boolean = false;
  print: any;
  isIgst: boolean = false;
  discountType: string;
  cess: number = 0;
  cessValue: number;
  discountValue: any;
  total: number;
  dueAmount: number;
  paidAmount: number = 0;
  subTotal: number = 0;
  saveType: string = "save";
  paymentTerms: any;
  active: boolean = true;
  paid: any;
  addCgst: number;
  addSgst: number;
  addIgst: number;
  totalTax: number;
  totalBeforeTax: number;
  cessAmount: number;
  fileId: string;
  fileName: any;
  invoiceDate: any;
  dueDate: any;
  discount: number = 0;
  items: Item[] = [];
  bankDetails: any;
  purchaseOrder: string;
  invoiceType: string;
  isEcommerce: boolean = false;
  ecommerceGSTIN: string;
  salesItems: Item = new Item();
  productItems: Item[] = [];
  serviceItems: Item[] = [];
  //stockItemId: StockItem = new StockItem();

  address: Address = new Address();
  isToggleEnable: boolean = false;
  isQuotationToggle: boolean = false;
  isCredit: boolean = true;
  isCreditHide: boolean = true;

  // description = "";
}



