import { StockItem } from "./stockitem.entity";
import { Service } from "./Service.entity";

export class Item {
  id: any = null;
  productId: number;
  serialNumber: string;
  line: string;
  quantity: any;
  price: number;
  unit: number;
  discount: number;
  location: string;
  print: string;
  description: string;
  cgst: number;
  igst: number;
  sgst: number;
  cgstAmount: number;
  sgstAmount: number;
  igstAmount: number;
  taxAmount: number = 0;
  totalAmount: number;
  totalAfterTax: number;
  active: boolean = true;
  taxrate: string;
  gstPercent: number;
  stockItemId: StockItem = new StockItem();
  service: Service = new Service();
  count: number;
  cessPercent: number;
  cessAmount: number;
  discountAmount: number;
  stockDisplayValue: string;
  greaterStock: boolean = false;
  isNewRow: boolean;
  itemsubtotalBeforeTax: number = 0;

  company: any;
  product: any;
  productCode: any;
  serviceCode: any;
  // taxrate: any;
  siNo: number = 1;
  serviceId: number;
  uqc: string;
  amount: number = 0;
  total: number = 0;
}



