import { Service } from "./Service.entity";
import { Inventory } from "./Product.entity";
import { Company } from "./company.entity";
import { StockItem } from "./stockitem.entity";
import { PurchaseInvoice } from "./purchaseinvoice.entity";
import { Customer } from "./customer.entity";
import { Item } from "./item.entity";

export class CRDN {
  id: string = "0";
  productId: StockItem = new StockItem();
  serviceId: Service = new Service();
  customer: Customer = new Customer();
  // items: Item = new Item();
  items: Item[] = [];
  invoiceNumber: any;
  totalBeforeTax: number;

  userId: any;
  companyId: number;
  crdnNumber: string;
  gstRegNumber: string;
  gstNo: string;
  quantityReturned: number;
  receiverName: string;
  supplierName: string;
  displayName: string;
  supplierId: string;
  receiptNumber: string;
  receiptDate: Date;
  purchaseInvoice: any;
  refundVoucherNumber: any;
  refundVoucherDate: Date;
  documentType: string;
  reason: string;
  amount: number;
  totalamount: number;
  isCgst: boolean = true;
  isIgst: boolean = false;
  total: number;

  // supplyState: string;
  refundVoucherValue: number;
  refundVoucher: number;

  applicableTaxRate: string;
  applicableTax: string;

  taxRate: string;
  taxableValue: any;
  taxable: any;

  cessAmount: any;
  cess: string;

  // urType: string;
}



