import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from "@angular/forms";
import { AppFormGroup } from "../shared/common/app.form";

export class CRDNFormGroup extends AppFormGroup {
  static init() {
    return this.getFormGroup();
  }

  static edit(group: FormGroup) {
    group.addControl(
      "crdn_document_type",
      this.getController(true, this.TYPE_ALL, null, null)
    );
  }

  static c(group: FormGroup) {
    group.addControl(
      "crdn_receiver_name",
      this.getController(true, this.TYPE_ALFA_NUM_DES_SPACE, 2, 255)
    );

    group.addControl(
      "crdn_refund_voucher_number",
      this.getController(true, this.TYPE_ALL, 2, 32)
    );

    group.addControl(
      "crdn_gst_reg_number",
      this.getController(false, this.GST_CODE_PATTERN, 15, 15)
    );

    group.addControl(
      "crdn_receipt_date",
      this.getController(null, this.TYPE_DATE, 8, 10)
    );

    group.addControl(
      "crdn_refund_voucher_value",
      this.getController(true, this.TYPE_DECIMAL, null, null)
    );

    group.addControl(
      "crdn_applicable_taxrate",
      this.getController(false, this.TYPE_ALL, null, 32)
    );

    group.addControl(
      "crdn_taxable_value",
      this.getController(false, this.TYPE_DECIMAL, null, null)
    );

    group.addControl(
      "crdn_cess_amount",
      this.getController(false, this.TYPE_DECIMAL, null, null)
    );

    group.addControl(
      "crdn_reason",
      this.getController(false, this.TYPE_ALL, null, 255)
    );
    group.addControl(
      "crdn_total",
      this.getController(false, this.TYPE_ALL, null, 255)
    );

    group.removeControl("pi_supplier");

    group.removeControl("crdn_gst_reg");

    group.removeControl("crdn_refund_voucher");
    group.removeControl("debit_receipt_date");

    group.removeControl("crdn_dreason");

    group.removeControl("crdn_applicable_tax");

    group.removeControl("crdn_taxable");

    group.removeControl("crdn_cess_damount");
    group.removeControl("crdn_dtotal");
  }

  // group.addControl(
  //   "crdn_number",
  //   this.getController(false, this.TYPE_ALL, null, null)
  // );

  static d(group: FormGroup) {
    group.addControl(
      "pi_supplier",
      this.getController(true, this.TYPE_DATA, null, null)
    );

    group.addControl(
      "crdn_gst_reg",
      this.getController(false, this.GST_CODE_PATTERN, 15, 15)
    );

    group.addControl(
      "crdn_refund_voucher",
      this.getController(true, this.TYPE_ALL, 2, 32)
    );
    group.addControl(
      "debit_receipt_date",
      this.getController(null, this.TYPE_DATE, 8, 10)
    );

    group.addControl(
      "crdn_dreason",
      this.getController(false, this.TYPE_ALL, null, 255)
    );

    group.addControl(
      "crdn_dtotal",
      this.getController(false, this.TYPE_ALL, null, 255)
    );

    group.addControl(
      "crdn_applicable_tax",
      this.getController(false, this.TYPE_ALL, null, 32)
    );

    group.addControl(
      "crdn_taxable",
      this.getController(false, this.TYPE_DECIMAL, null, null)
    );

    group.addControl(
      "crdn_cess_damount",
      this.getController(false, this.TYPE_DECIMAL, null, null)
    );
    group.addControl(
      "crdn_amount",
      this.getController(false, this.TYPE_DECIMAL, null, null)
    );

    group.removeControl("crdn_receiver_name");

    group.removeControl("crdn_refund_voucher_number");

    group.removeControl("crdn_gst_reg_number");

    group.removeControl("crdn_receipt_date");

    group.removeControl("crdn_refund_voucher_value");

    group.removeControl("crdn_applicable_taxrate");

    group.removeControl("crdn_taxable_value");

    group.removeControl("crdn_cess_amount");

    group.removeControl("crdn_reason");
    group.removeControl("crdn_total");
  }
}



